@font-face{
  font-family: 'Atlas Grotesk';
  src: url('assets/fonts/AtlasGrotesk-Light.woff') format('woff');
  font-weight: 100;
}
@font-face{
  font-family: 'Atlas Grotesk';
  src: url('assets/fonts/Atlas Grotesk Web-Regular.woff') format('woff');
  font-weight: 200;
}
@font-face{
  font-family: 'Atlas Grotesk';
  src: url('assets/fonts/Atlas Grotesk Web-Bold.woff') format('woff');
  font-weight: 300;
}
@font-face{
  font-family: 'Cerebri Sans';
  src: url('assets/fonts/CerebriSans-HeavyItalic.woff') format('woff');
  font-weight: 500;
}
@font-face{
  font-family: 'Cerebri Sans';
  src: url('assets/fonts/CerebriSans-ExtraBold.woff') format('woff');
  font-weight: 400;
}
@font-face{
  font-family: 'Cerebri Sans';
  src: url('assets/fonts/CerebriSans-Medium.woff') format('woff');
  font-weight: 300;
}
@font-face{
  font-family: 'Cerebri Sans';
  src: url('assets/fonts/CerebriSans-MediumItalic.woff') format('woff');
  font-weight: 200;
}
html {
  overflow-y: scroll;
}
.app-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
div#root {
  top:0;
  position: relative;
  min-height: 100%;
}
body {
  padding: 0;
  margin: 0;
  top:0;
  font-family: 'Atlas Grotesk', sans-serif;
  font-weight: 100;
  color: #1a1a1a;
}
p {
  font-size:1.5em;
}
body.page-background {
  bottom:0;
  height:100vh;
  max-height:100%;
  width:100vw;
  max-width:100%;
}
.content {
  margin: 0 10vw;
  padding-bottom:3em;
}
main {
  padding-bottom:3em;
}
a {
  text-decoration: none;
  color:#000000;
}
.content a {
  color:#3DC5C9;
}
.content p,li {
  line-height: 1.5em;
}
.content li {
  list-style-type: disc;
}
.content pre  {
  padding: 3vw;
  overflow: auto;
}

.header-title {
  font-family: 'Cerebri Sans', sans-serif;
  font-weight: 500;
  font-size: 2rem;
  margin: 0 2vw 0 2vw;
  color: #50CFB6;
}

header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
header.app-header {
  width: 100vw;
  max-width: 100%;
  text-align: center;
  padding: 5vh 0 5vh 0;
  left:0;
  top:0;
  height:10vh;
}
li.nav-li {
  display: inline-block;
  padding: 0 2vw 0 2vw;
  font-size: 1em;
}
ul.nav-ul {
  padding: 0;
}
.active {
  color: #50CFB6;
}
h1 {
  font-size: 3em;
  font-family: 'Cerebri Sans', sans-serif;
  font-weight: 400;
  text-transform: capitalize;
}
h2 {
  text-align: center;
}
footer {
  margin: auto;
  text-align: center;
  position: absolute;
  width:100vw;
  max-width: 100%;
  bottom: 0;
  height:3em;
}
footer p {
  margin: 0;
  padding: 2.5vh;
  font-size: 0.5em;
}
.title-active > h1{
  color:black;
}
.special-sidebar {
  text-align: right;
  z-index: -10;
}

@media (max-width: 812px) {
  .app-container {
    width: 100vw;
  }
  .app-header {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .header-title {
    font-size: 1.8rem;
  }
  .nav-ul {
    font-size: 0.5rem;
    margin-top: 20px;
  }
  .about-link::after {
    content: ' this website';
  }
  .about-link {
    text-transform: lowercase;
    border: 1px solid #50CFB6;
    padding: 10px;
    border-radius: 4px;
  }
}


